@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

body {
  color: #4a4a4a;
  font-family: "Roboto";
  overflow-x: hidden;
  background-color: #f8f8f8;
}

button{border:none;cursor:pointer;background-color:#f8f8f8}button .iconButton{height:24px;width:24px}.nav-bar{width:90vw;height:10vh;display:flex;align-items:center;margin-right:5%;margin-left:5%}.nav-bar div:last-child{margin-left:auto}.logo{width:150px;margin-left:40px}.RightMenu{float:right}

button{border:none;cursor:pointer;background-color:#f8f8f8}button .iconButton{height:24px;width:24px;color:#4a4a4a}.contactBtnGrp{display:flex;align-items:center;color:#4a4a4a}.contactBtnGrp .contactText{font-weight:900;margin-left:10px}@media only screen and (max-width: 800px){.userProfileBtn{display:none}.logoutBtn{display:none}.contactBtn{display:none}}

.applicantsOverview{background-color:white;width:90vw;margin-right:5%;margin-left:5%;padding-top:20px}

.applicantTopbar{display:flex;flex-direction:row}.applicantTopbar .iconButton{margin-left:20px;height:24px;width:24px}.applicantTopbar div:last-child{margin-left:auto}.applicantTopbar .leftContainer{display:flex;flex-direction:row;align-items:center;justify-content:center}.applicantTopbar span{font-size:20px;font-weight:bold;text-align:center;vertical-align:middle;margin-left:20px;color:#4a4a4a}

.statsBar{display:inline-flex;flex-direction:row}.statsBar .stat:last-child{border-right:none}@media only screen and (max-width: 800px){.statsBar{display:none}}

.stat{width:100px;text-align:center;border-right:1px solid #ddd}.stat .number{font-size:20px;font-weight:bold;color:#4a4a4a}.stat .label{font-size:12px;color:#4a4a4a}

.filtersBar{padding-left:20px;padding-top:20px;margin-bottom:20px}.filtersBar .searchBox{display:inline-flex;align-items:center}.filtersBar .searchBox input{border:1px solid #dfdfdf;width:300px;padding:10px 0 10px 40px}.filtersBar .icon{position:absolute;padding-left:10px;width:30px;height:30px}.filtersBar .bidsFilter,.filtersBar .statusFilter{display:inline}.filtersBar .bidsFilter select,.filtersBar .statusFilter select{margin-left:15px;border:1px solid #dfdfdf;color:#888;width:70px;padding:10px 0 10px 5px}@media only screen and (max-width: 800px){.filtersBar .searchBox{display:block;margin-bottom:10px}.filtersBar .bidsFilter select{margin-left:0}}

.applicantsGroup .groupTitle{margin-left:20px}.applicantsGroup .spinner{display:flex;align-items:center;justify-content:center;height:300px}.applicantsGroup .timeoutError{display:flex;align-items:center;justify-content:center;height:300px}@media only screen and (max-width: 800px){.applicantsContainer{white-space:nowrap;-webkit-overflow-scrolling:touch}.applicantsContainer::-webkit-scrollbar{display:none}.applicantsContainer .applicant{display:inline-block}}

.applicant{width:300px;height:250px;padding-top:20px;padding-bottom:20px;display:inline-flex;align-items:center;flex-direction:column;border:1px solid #e9e7e7;margin:20px 5px 20px 20px}.applicant .pinkCircle{height:70px;width:70px;background-color:#fee8d3;color:#cd6705;border-radius:50%;text-align:center;line-height:70px;font-weight:bold;margin:auto}.applicant .blueCircle{height:70px;width:70px;background-color:#d3eefe;color:#0583cd;border-radius:50%;text-align:center;line-height:70px;font-weight:bold;margin:auto}.applicant .greenCircle{height:70px;width:70px;background-color:#d3fed7;color:#05cd17;border-radius:50%;text-align:center;line-height:70px;font-weight:bold;margin:auto}.applicant .yellowCircle{height:70px;width:70px;background-color:#f5fed3;color:#a3cd05;border-radius:50%;text-align:center;line-height:70px;font-weight:bold;margin:auto}.applicant .ntfyContainer{margin-top:5px;display:flex;justify-content:center;align-items:center}.applicant .greyNtfy{background-color:#9d9d9d;color:white;display:inline-block;padding:2px 5px;border-radius:5px;font-size:12px;margin:2px 0;text-transform:uppercase;margin:auto}.applicant .orangeNtfy{background-color:#feb902;color:white;display:inline;padding:2px 5px;border-radius:5px;font-size:12px;margin:2px 0;text-transform:uppercase}.applicant span{display:block;text-align:center;margin:5px 0 5px 0;width:100%}

